@charset "UTF-8";

body {
  background: url(data:image/jpeg;base64,)
    repeat 0 0;
  font-family: sans-serif;
}
.tip-container {
  background-color: #ecebff;
  width: 100%;
  max-width: 320px;
  min-width: 320px;
  min-height: 320px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 100px auto 0;
  padding: 50px;
  border: 5px solid #ccf;
  border-radius: 50%;
}
.tip-container h1 {
  color: #ccf;
}
.tip-container div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 15px;
}

.tip-container input {
  background-color: #fff;
  width: 140px;
  height: 40px;
  font-size: 18px;
  padding-left: 10px;
  border: none;
}
.tip-container .select-container label {
  padding-top: 30px;
}
.tip-container .select-wrapper {
  position: relative;
  width: 150px;
}
.tip-container .select-wrapper::after {
  color: black;
  content: 'â–¾';
  margin-right: 10px;
  pointer-events: none;
  position: absolute;
  top: 10px;
  right: 17px;
  font-size: 18px;
}

.tip-container select {
  -moz-appearance: none;
  -webkit-appearance: none;
  background: white;
  border: none;
  border-radius: 0;
  cursor: pointer;
  padding: 12px;
  width: 100%;
  font-size: 18px;
  border: none;
}
.tip-container .select:focus {
  color: black;
}
.tip-container .select::-ms-expand {
  display: none;
}
.tip-container button {
  background-color: #d32539;
  color: #fff;
  padding: 10px 15px;
  border-radius: 15px;
  border: none;
  cursor: pointer;
}
